@import url(https://fonts.googleapis.com/css?family=Rajdhani:300,400);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background:-webkit-linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/SW-app/static/media/bryan-goff-395922-unsplash.33f70011.jpg);
  background:linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/SW-app/static/media/bryan-goff-395922-unsplash.33f70011.jpg);
  font-family: 'Rajdhani', sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-logo img{
    height: 100px;
    width: auto;
    padding: 20px 0 0 20px;
}
.option{
    color: #fff;
    font-size: 130%;
    font-weight: 300;
    margin-bottom: 10px;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.option:hover{
    cursor: pointer;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}
